section.Admin {
  height: 100%;
  overflow: auto;
  background: #fdfdfd; }
  section.Admin > header {
    background-color: #456;
    color: white;
    position: relative;
    z-index: 2; }
    section.Admin > header .title {
      display: inline-block;
      font-size: 1.5em;
      margin: 25px;
      text-align: center; }
  section.Admin > .content {
    position: relative;
    font-size: 14px;
    line-height: 1.5; }
  section.Admin section.Navigation {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 2; }
  section.Admin a,
  section.Admin .clickable {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  section.Admin .clickable:hover {
    background: #fff; }
  section.Admin .clickable:active {
    background: #ddd; }
  section.Admin select,
  section.Admin textarea,
  section.Admin input {
    font-size: 16px; }



